@import url(https://fonts.googleapis.com/css2?family=Baloo+Paaji+2:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Barlow+Condensed:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Francois+One&family=Press+Start+2P&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  margin: 0;
  padding: 0;
}



img { 
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
     user-select: none;
 }

 html {
    scroll-behavior: smooth;
}
.NavBar
{
    position: fixed;
    z-index: 10;
}
@media (min-width: 1000px) {
    .NavBar
    {
        margin: 0;
        width: 100%;
        height: 10vh;
        min-height: 50px;
        display: flex;
        justify-content: space-between;
        align-items: center ;
        background-color: #164987;
        box-shadow: -10px -10px blue;
    }
}

.NavBar > .Menu
{
    display: flex;
    width: 40%;
    justify-content: space-around;
    color: white
}

.NavBar > .Menu > *:hover
{
    color: red;
    cursor: pointer;
}

@media (max-width: 1000px) {
    .NavBar
    {
        width: 100%;
        text-align: center;
        align-items: center ;
        background-color: #164987;
        padding-bottom: 1vh;
    }
    .NavBar > .Menu
    {
        display: none
    }
}

.Icons > *:hover
{
    cursor: pointer;
}

.Icons > a > *
{
    color: white;
}

.WelcomePage > .NavBar > h1
{
    color:white;
    font-family: 'Baloo Paaji 2', cursive;
    font-size: 2em;
    margin: 0;
    padding: 10px;
    margin-left: 15px;
}

.Content
{
    margin: 0;
}

.Section
{
    margin: 0;
    background-color:#72a7fc;
    padding-top: 8vh;
    padding-bottom: 3vh;
    min-height: 95vh;
}

.Content > .Section > .Page
{
    padding-top: 10vh;
    min-height: 80vh;
    position: relative;
    margin-left: 30%;
    width: 40%;
    color: white;
    font-family: 'Barlow Condensed', sans-serif;
    text-align: justify;
}


.Page > .Text > h1{
    font-size: 5em;
    margin-bottom: 75px;
    text-align: center;
    background-color: white;
    color: #72a7fc;
    margin: 0;
    border: 1px solid gray
}

.Page > .Text > h2
{
    font-size: 2em;
    letter-spacing: 2px;
}

.Projects
{
    width: 100%;
    padding-top: 10vh;
    min-height: 90vh;
    position: relative;
    text-align: center;
    font-family: 'Barlow Condensed', sans-serif;
    color: white;
    background-color: #fcfcfc;
}

.Projects > h1
{
    font-size: 3.5em;
    width: 40%;
    background-color: #164987;
    color: white;
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 10vh;
    padding: 10px;
    border: 1px solid gray
}

.Skills
{
    padding-top: 10vh;
    min-height: 90vh;
    background-color: #72a7fc;
    position: relative;
    text-align: center;
}

.ProfileImage
{
    margin-bottom: 3vh;
    border-radius: 50%;
    background-color: #d9d9d9;
    background-image: linear-gradient(315deg, #d9d9d9 0%, #f6f2f2 74%);
}

.Page > .ImageWrapper
{
    width: 100%;
    display: flex;
    justify-content: center;
}

@media (max-width: 1000px) {
    .Content > .Section > .Page
    {
        width: 80%;
        margin-left: 10%;
        text-align: justify;
    }
}

.Skills > h1
{
    font-size: 3.5em;
    width: 40%;
    background-color: white;
    color: #72a7fc;
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 10vh;
    padding: 10px;
    border: 1px solid gray;
}

.MainDashBoard
{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    min-height: 70vh;
    
}

.SkillDashboard
{
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-wrap: wrap;
    width: 80%;
    justify-content: space-around;
    min-height: 20h;
}

.Page > h1
{
    font-weight: bolder;
    font-size: 3em;
    text-align: center;
}
.Carousel
{
    width: 100%;
    position: relative;
}

@media (min-width: 1000px) {
    .Carousel > .Card > .Text
    {
        margin-left: 1%;
        width: 60%;

    }
    .Carousel > .Card > .ImageDiv
    {
        max-width: 40%;
    }
}

.Carousel > .Card > .Text > h3
{
    text-align: justify;
}

.Carousel > .Name
{
    margin-bottom: 3vh;
}

.Carousel > .Name > h1{
    font-size: 2.5em;
    display: inline;
}

.Carousel > .Name > h1:hover
{
    cursor: pointer;
}

.Card {
    display: flex;
    width: 60%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 5vh;
    padding: 15px;
    color: black;
    border-radius: 50px;
    position: relative;
}
.Carousel > .Card > .ImageDiv
{
    height:auto;
    max-width: 40%;
    background-color: #164987;
    margin-bottom: auto;
    margin-top: auto;
    padding: 1vh;
}

.ImageDiv > img
{
    max-width: 100%;
    height: auto;
}

.Card > .Text > h1
{
    color: #164987;
    font-size: 2.5em;
    font-weight: bolder;
}

.Card > .Text > h3
{
    
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: lighter;
    padding-left: 1vw;
}

@media (max-width: 1000px) {
    .Card
    {
        flex-flow: column;
        width: 80%;
    }
    .Card > .ImageDiv
    {
        margin: 0;
        max-width: 100%;
    }
    .Card > .Text
    {
        margin-left: 1%;
    }
    .Carousel > .Card > .ImageDiv
    {
        max-width: 100%;
    }
}



h3 > a
{
    color: black
}

.NN
{
    background-color: #fcfcfc;
}

.SkillImage
{
    background-color: white;
    padding: 10px;
    border-radius: 50px;
    border: 1px solid gray;
    box-shadow: 10px 10px 10px #3080ff;
    transition: all 0.5s;
    margin: 2vh;
    min-height: 200px;
}

.CardImage
{
    border-radius: 50px;
    width: 200px;
    font-family: 'Francois One', sans-serif;
    margin: 0;
    position: relative;
    height: 100%;
    display: flex;
    flex-flow: column;
    justify-content: center;
    min-width: 20vh;
}

.CardImage > h1
{
    color:  #164987;
    border-radius: 50px;
    padding: 10px;
    margin: 0;
}

.SkillImage:hover
{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
}

.ProgressBar
{
    position: relative;
    width: 80%;
    height: 20%;
    margin-left: 10%;
    margin-right: auto;
    border: 1px solid #164987;
    border-radius: 50px;
    display: flex;
}

.ProgressBar > *
{
    background-color: #164987;
    border-top-left-radius: 50px;
    border-bottom-left-radius: 50px;
}
.Footer
{
    background-color: #164987;
}
.Icons
{
    margin: 0;
    width: 50%;
    margin-left: 25%;
    color: white;
    display: flex;
    justify-content: space-around;
    padding: 25px;
    background-color: #164987;
}

.Icons > *
{
    -webkit-transform: scale(1.8);
            transform: scale(1.8);
}
